import tarbar from "@/components/tarbar/tarbar"
export default {
	onShareAppMessage(res) {
		return {
			title: this.key,
			path: 'pages/goods/goods-list/product/product?key=' + this.key,
			success: (res) => {
				console.log(res);
			},
			fail: (res) => {
				console.log(res);
			}
		}
	},
	components: {
		tarbar
	},
	data() {
		return {
			scrollTop: 0,
			key: '',
			type: '',
			goodsList: [],
			loadingText: "正在加载中...",
			pageCount: 1,
			pageid: 1,
			selectIndex: '2',
			showTabbar:false
		};
	},
	onLoad: function(option) {
		this.type = option.key
		if (this.type == 1) {
			this.key = '爆款推荐',
				this.selectIndex = 2
		} else if (this.type == 2) {
			this.key = '10元专区'
			this.selectIndex = 3
		} else if (this.type == 3) {
			this.key = '清仓特卖'
			this.selectIndex = 4;
			this.showTabbar=true;
		}
		uni.setNavigationBarTitle({
			title: this.key
		});
		this.reload();
		//兼容H5下排序栏位置










	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop;
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		this.goodsList = []
		setTimeout(() => {
			this.reload();
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		this.pageid++;
		if (this.pageid > this.pageCount) {
			this.loadingText = "到底了";
			return false;
		} else {
			this.reload();
			this.loadingText = "正在加载...";
		}
	},
	methods: {
		reload() {

			let data = {
				type: this.type,
				p: this.pageid
			}
			this.$xm.post('/Product/goodslist', data, (res) => {
				console.log(res);
				if (res) {
					res.data.map(ele => {
						ele.pro_sn = 'http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
						ele.pro_name = ele.pro_name.slice(0, 15) + '...'
					})


					this.goodsList = this.goodsList.concat(res.data);
					this.pageCount = res.count;
					if (this.pageCount == 1) {
						this.loadingText = '到底了'
					}

				} else {
					this.loadingText = "暂无商品！";
				}

			})
		},

		//商品跳转
		toGoods(e) {
			uni.navigateTo({
				url: '../../goods?gid=' + e.id
			});
		},
		// 加入购物车
		joinCart(e) {
			let data = {
				proid: e.id,
				pronum: 1
			}
			this.$xm.post('/Cart/add', data, (res) => {
				let result = res.msg;
				uni.showToast({
					title: result
				});
			})
		},

	}

}
